// Import site utilities.
@import '../../global/utils/init';

.l-header {
	box-shadow: 0 0 5px;
    background: #014968;
    width: 100%;
    z-index: 999;
    max-width: 100vw;
    margin: 0 auto;
    display: flex;
    justify-content: center;
    .block-system-header {
    	flex-basis:600px;
    }
    .block-system-main-menu {
    	li {
    		border: 0;
			padding-top: 10px;
			a {
				color: #ffffff;
				font-size: 16px;
				font-weight: 600;
				padding: 0px 20px;
				text-decoration: none !important;
				&:after {
				    background: #cf5840;
				    content: ' ';
				    display: block;
				    height: 2px;
				    transition: width .2s;
				    width: 0%;
				}
				&:hover {
					background: none;
					padding: 0;
					&:after {
						width: 100%;
					}
				}
				&.active {
					&:after {
						width: 100%;
					}
				}
			}
			&:hover {
				background-color: none !important;
			}
		}
    }
	.header-site-name-wrapper {
		img {
			max-height: 90px;
		}
	}
	.header-menu {
		
	}
}



